.is-hidden {
  display: none;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
li {
  list-style: none;
  outline: 1px solid black;
  margin: 0;
  padding: 10px;
  width: 350px;
}
.json-container,
.weather-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

p {
  margin: 0;
  padding: 0;
}

.loader {
  width: 48px;
  height: 48px;
  border: 3px dotted #764ed1;
  border-style: solid solid dotted dotted;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  box-sizing: border-box;
  animation: rotation 2s linear infinite;
}
.loader::after {
  content: '';
  box-sizing: border-box;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  border: 3px dotted #ff3d00;
  border-style: solid solid dotted;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  animation: rotationBack 1s linear infinite;
  transform-origin: center center;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}

.loader-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.breed-select {
  width: 30%;
}

.error-container {
  text-align: center;
  margin-top: 20px;
}

/* Стилі для основного select */
.breed-select {
  font-family: Arial, sans-serif;
  font-size: 18px;
  padding: 8px;
  border: 2px solid yellowgreen;
  border-radius: 4px;
  cursor: pointer;
  background-color: #dfe2ab;
}

/* Стилі для опцій */
.option-item {
  /* Загальні властивості */
  font-size: 16px;
  padding: 6px;
  cursor: pointer;
}

/* Наприклад, стилізація при наведенні на опцію */
.option-item:hover {
  background-color: #dfe2ab;
  cursor: pointer;
}

.is-shown {
  display: none;
}
